//引入仓库，状态机
import { createNamespacedHelpers } from 'vuex';
const { mapActions, mapState, mapMutations } = createNamespacedHelpers('pet');
export default {
	data() {
		return {
			order: [],
			target: false,
			//总金额
			allmoney: 0,
			//收货电话
			phone: '',
			//收货地址
			profile: '',
			//收货人
			consignee: ''
		};
	},
	onLoad() {
		try {
			this.order = uni.getStorageSync('arry');
		} catch (e) {}
		//总金额
		for (var i = 0; i < this.order.length; i++) {
			this.allmoney += this.order[i].price * this.order[i].count;
		}
	},
	computed: {},
	mounted() {},
	methods: {
		...mapActions(['createOrder']),
		//点击结算出现地址兰
		btn() {
			this.target = true;
		},
		//订单提交事件
		indent(){
			this.order.forEach(item => {
				//电话号码
				item.phone=this.phone,
				//收货地址
				item.profile=this.profile,
				//收货人
				item.consignee=this.consignee
			})
			this.createOrder(this.order)
			uni.showModal({
			    title: '成功',
			    content: '支付成功！！谢谢',
			})
			uni.switchTab({
				url: '/pages/ShoppingCart/ShoppingCart'
			});
			
		},
		//点击取消事件回到购物车
		offorder(){
			uni.switchTab({
				url: '/pages/ShoppingCart/ShoppingCart'
			});
		}
		
		
	}
};
